@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap');
@import 'normalize.css';

:root {
    --clr-dark-900: #2f3640;
    --clr-dark-800: #353b48;
    --clr-white: #f5f6fa;
}

*,
*::before,
*::after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html,
body,
#root {
    height: 100%;
}

body {
    background-color: var(--clr-dark-800);
}

button {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    cursor: pointer;
}

img {
    max-width: 100%;
    display: block;
}

.btn {
    min-width: 10mm;
    min-height: 10mm;
    border-radius: 0.4em;
    padding: 0.5em 1em;
    color: var(--clr-white);
    background-color: var(--clr-dark-900);
    outline: solid transparent;
    font-size: 1.25em;
    font-weight: 600;
}

.btn:active {
    transform: translateY(1px);
    filter: saturate(150%);
}

.btn:hover,
.btn:focus {
    color: var(--clr-dark-900);
    border-color: currentColor;
    background-color: var(--clr-white);
}
